body {
  margin: 0;
  padding: 0; }

.main-header {
  background: #1b2d37 url("../img/header-background.png"); }

.main-header__top {
  max-width: 1410px;
  margin: auto;
  padding-left: 33px;
  padding-right: 33px;
  display: flex;
  align-items: flex-start; }

.main-header__contacts {
  display: flex;
  margin-top: 37px;
  align-items: flex-start; }

.main-header__link {
  margin-top: 12px;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #ffffff;
  position: relative;
  text-decoration: none; }

.main-header__link:hover {
  text-decoration: underline;
  opacity: 0.7; }

.main-header__link--email {
  margin-right: 88px;
  padding-left: 39px; }

.main-header__link--email::before {
  content: "";
  position: absolute;
  display: block;
  width: 19px;
  height: 14px;
  top: 3px;
  left: 0;
  background: url("../img/email-desktop-icon.svg") 50% 50% no-repeat; }

.main-header__link--tel {
  margin-right: 49px;
  padding-left: 34px; }

.main-header__link--tel::before {
  content: "";
  position: absolute;
  display: block;
  width: 18px;
  height: 18px;
  top: 0;
  left: 0;
  background: url("../img/phone-desktop-icon.svg") 50% 50% no-repeat; }

.main-header__bottom {
  max-width: 1410px;
  margin: auto;
  overflow: auto;
  background: url("../img/plates-desktop.png") 100% 63% no-repeat, url("../img/header-shape-desktop.png") 89% 61% no-repeat;
  position: relative; }

.main-header__scroll {
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  color: white;
  opacity: 0.5;
  position: absolute;
  bottom: 128px;
  left: -6px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  transform: rotate(-90deg); }

.main-header__info {
  width: 720px;
  margin-top: 227px;
  margin-bottom: 275px;
  margin-left: 30px; }

.main-header__title {
  margin-top: 0;
  margin-bottom: 30px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 80px;
  line-height: 94px;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.07em; }

.main-header__text {
  margin-top: 0;
  margin-bottom: 43px;
  margin-left: 98px;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 22px;
  line-height: 34px;
  font-weight: 400;
  color: #ffffff;
  position: relative; }

.main-header__text::before {
  content: "";
  position: absolute;
  display: block;
  width: 66px;
  height: 2px;
  left: -94px;
  top: 17px;
  background: #ffffff; }

@media (max-width: 1023px) {
  .main-header__top {
    padding-left: 25px;
    padding-right: 25px; }
  .main-header__contacts {
    margin-top: 32px;
    align-items: center; }
  .main-header__link {
    padding: 0;
    margin: 0;
    background: white; }
  .main-header__link--email {
    display: block;
    font-size: 0;
    width: 25px;
    height: 19px;
    padding: 0;
    margin-right: 34px;
    background: url("../img/email-tablet-icon.svg") 50% 50% no-repeat; }
  .main-header__link--email::before {
    display: none; }
  .main-header__link--tel {
    display: block;
    font-size: 0;
    width: 24px;
    height: 24px;
    padding: 0;
    background: url("../img/phone-tablet-icon.svg") 50% 50% no-repeat; }
  .main-header__link--tel::before {
    display: none; }
  .main-header__bottom {
    background: url("../img/plates-tablet.png") 100% 15% no-repeat, url("../img/header-shape-tablet.png") 100% 4% no-repeat; }
  .main-header__scroll {
    display: none; }
  .main-header__info {
    width: 470px;
    margin-top: 115px;
    margin-bottom: 163px;
    margin-left: 88px; }
  .main-header__title {
    font-size: 60px;
    line-height: 70px; }
  .main-header__text {
    margin-left: 51px;
    font-size: 18px;
    line-height: 28px;
    margin-bottom: 54px; }
  .main-header__text::before {
    left: -48px;
    width: 33px; } }

@media (max-width: 767px) {
  .main-header__top {
    padding-left: 17px;
    padding-right: 17px; }
  .main-header__contacts {
    margin-top: 22px; }
  .main-header__link--email {
    margin-right: 22px; }
  .main-header__bottom {
    background: url("../img/plates-mobile.png") 100% 10% no-repeat, url("../img/header-shape-mobile.png") 100% 6% no-repeat; }
  .main-header__scroll {
    display: none; }
  .main-header__info {
    width: auto;
    margin-top: 230px;
    margin-bottom: 70px;
    margin-left: 15px;
    margin-right: 15px; }
  .main-header__title {
    font-size: 40px;
    line-height: 47px;
    margin-bottom: 27px; }
  .main-header__text {
    margin-left: 50px;
    font-size: 16px;
    line-height: 25px;
    margin-bottom: 40px; }
  .main-header__text::before {
    left: -46px;
    top: 16px; } }

.button {
  box-sizing: border-box;
  display: inline-block;
  vertical-align: top;
  text-align: center;
  padding-top: 12px;
  padding-bottom: 15px;
  padding-left: 26px;
  padding-right: 27px;
  border-radius: 5px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  color: #ffffff;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer; }

.button:hover {
  opacity: 0.7; }

.button--white-border {
  border: 1px solid #ffffff; }

.button--callback {
  margin-left: 98px;
  font-size: 16px;
  line-height: 19px;
  padding-top: 17px;
  padding-bottom: 20px;
  padding-left: 45px;
  padding-right: 45px;
  border: none;
  background: #00b2ff; }

.button--callback-mobile {
  display: none; }

@media (max-width: 1023px) {
  .button--white-border {
    display: none; }
  .button--callback {
    margin-left: 52px;
    padding-top: 17px;
    padding-bottom: 20px;
    padding-left: 30px;
    padding-right: 30px; } }

@media (max-width: 767px) {
  .button--callback {
    display: none; }
  .button--callback-mobile {
    display: inline-block;
    width: 100%;
    font-size: 16px;
    line-height: 19px;
    padding-top: 17px;
    padding-bottom: 20px;
    padding-left: 32px;
    padding-right: 32px;
    border: none;
    background: #00b2ff; } }

.logo {
  display: flex;
  margin-top: 33px;
  margin-right: auto; }

.logo__image-link {
  display: block;
  width: 52px;
  height: 52px; }

.logo__image-link:hover {
  opacity: 0.7;
  cursor: pointer; }

.logo__image {
  display: block;
  width: 52px;
  height: 52px; }

.logo__wrap {
  padding-left: 16px; }

.logo__text-link {
  display: block;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  line-height: 29px;
  font-weight: 700;
  color: #ffffff;
  margin-top: 5px;
  margin-bottom: 2px; }

.logo__text-link:hover {
  opacity: 0.7;
  cursor: pointer; }

.logo__text {
  display: block;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
  color: #788891; }

@media (max-width: 1023px) {
  .logo {
    margin-top: 20px; } }

@media (max-width: 767px) {
  .logo {
    margin-top: 17px; }
  .logo__image-link {
    width: 38px;
    height: 38px; }
  .logo__image {
    width: 38px;
    height: 38px; }
  .logo__wrap {
    display: none; } }

.main-content {
  max-width: 1260px;
  margin: auto;
  overflow: hidden;
  padding-top: 112px; }

@media (max-width: 1023px) {
  .main-content {
    padding-top: 54px; } }

@media (max-width: 767px) {
  .main-content {
    padding-top: 37px; } }

.features {
  padding-left: 30px;
  padding-right: 30px; }

.features__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

@media (max-width: 1023px) {
  .features {
    padding-left: 25px;
    padding-right: 25px; } }

@media (max-width: 767px) {
  .features {
    padding-left: 15px;
    padding-right: 15px; } }

.feature {
  width: calc(50% - 15px);
  min-height: 299px;
  box-sizing: border-box;
  margin-right: 0;
  margin-bottom: 30px;
  padding-top: 94px;
  padding-bottom: 10px;
  padding-left: 45px;
  padding-right: 50px;
  border: 1px solid #ebebeb;
  border-left: none;
  position: relative; }

.feature--secret,
.feature--cost {
  margin-bottom: 0; }

.feature__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 24px;
  line-height: 34px;
  font-weight: 400;
  color: #00b2ff;
  margin-top: 0;
  margin-bottom: 15px; }

.feature__text {
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #666666;
  margin-top: 0;
  margin-bottom: 0; }

.feature::before {
  content: "";
  position: absolute;
  left: 0;
  top: -1px;
  width: 3px;
  height: calc(100% + 2px);
  background: #00b2ff; }

.feature__icon {
  fill: #00b2ff;
  position: absolute; }

.feature__icon--speed {
  left: 44px;
  top: 39px;
  width: 29px;
  height: 35px; }

.feature__icon--providers {
  left: 45px;
  top: 39px;
  width: 35px;
  height: 35px; }

.feature__icon--secret {
  left: 45px;
  top: 42px;
  width: 36px;
  height: 31px; }

.feature__icon--cost {
  left: 45px;
  top: 39px;
  width: 35px;
  height: 35px; }

.feature:hover {
  border-color: #00b2ff;
  background: #00b2ff;
  color: white; }

.feature:hover .feature__icon {
  fill: #ffffff; }

.feature:hover .feature__title {
  color: white; }

.feature:hover .feature__text {
  color: white; }

@media (max-width: 1023px) {
  .feature {
    min-height: 306px;
    padding-top: 65px;
    padding-left: 27px;
    padding-right: 20px; }
  .feature--providers {
    padding-right: 30px; }
  .feature--secret,
  .feature--cost {
    min-height: 232px;
    margin-bottom: 0; }
  .feature__title {
    margin-bottom: 8px; }
  .feature::before {
    width: 2px; }
  .feature__icon--speed {
    top: 24px;
    left: 28px; }
  .feature__icon--providers {
    top: 21px;
    left: 25px; }
  .feature__icon--secret {
    top: 23px;
    left: 24px; }
  .feature__icon--cost {
    top: 22px;
    left: 25px; }
  .feature--secret {
    padding-top: 62px; }
  .feature__title--secret {
    margin-bottom: 14px; } }

@media (max-width: 767px) {
  .feature {
    width: 100%;
    min-height: 232px;
    padding-top: 70px;
    padding-bottom: 26px;
    padding-right: 14px;
    margin-bottom: 20px; }
  .feature--providers {
    padding-right: 10px; }
  .feature__title {
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 8px; }
  .feature--secret {
    padding-top: 64px; }
  .feature__title--secret {
    padding-right: 10px;
    margin-bottom: 0; }
  .feature__text--secret {
    margin-top: -9px; }
  .feature--cost {
    padding-top: 67px;
    margin-bottom: 0; }
  .feature__title--cost {
    margin-bottom: 10px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }
